.container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Bebas-Neue";
  background-color: white;
  gap: 1rem;
}

.bg {
  position: fixed;
  top: -100px;
  width: clamp(100%, 180vh, 200vh);
  opacity: 0.3;
  z-index: -99;
}

.header {
  font-size: 4rem;
}

.form {
  width: 500px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.field {
  display: flex;
  flex-direction: column;
}

.field > label {
  font-size: 2rem;
}

.field > input {
  height: 1.75rem;
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
  outline: none;
  border: 1px solid gray;
  border-radius: 5px;
  font-size: 1.25rem;
  color: rgb(83, 82, 82);
}

.submit {
  display: flex;
  justify-content: center;
}

.submit > button {
  width: 9rem;
  height: 2.25rem;
  border: none;
  border-radius: 15px;
  background-color: black;
  font-family: "Bebas-Neue";
  font-size: 2rem;
  color: white;
}

.submit > button:hover {
  cursor: pointer;
  color: white;
  background-color: gray;
  transition: 100ms;
}

.createAccount {
  display: flex;
  justify-content: center;
  gap: 0.25rem;
  font-size: 1.25rem;
}

.link {
  text-decoration: none;
  color: gray;
}
